




























import Vue from 'vue';
import { mapGetters } from 'vuex';

import store from '@/store';

interface HTMLInputEvent extends Event {
    target: HTMLInputElement & EventTarget;
}

export default Vue.extend({
  name: 'Upload',
  computed: {
    ...mapGetters('step', [
      'step',
      'stepDescription',
    ]),
    ...mapGetters('photo', [
      'base64',
    ]),
  },
  methods: {
    setBase64(e: HTMLInputEvent) {
      const file: File = (e.target.files as FileList)[0];
      const reader = new FileReader();
      reader.onload = (e: Event) => {
        store.dispatch('photo/setBase64', reader.result);
      };
      reader.readAsDataURL(file);
    },
    handleGetPhoto() {
      (this.$refs.inputFile as HTMLInputElement).click();
    },
    async handleUploadPhoto() {
      await store.dispatch('photo/sendPhoto');
      (this.$refs.inputFile as HTMLInputElement).value = '';
      await store.dispatch('step/nextStep');
    },
  },
  destroyed(): void {
    store.dispatch('photo/deleteBase64');
  },
});
