import { Loader } from '@/store/interfaces';

const state = {
  isLoading: false,
};
const mutations = {
  LOADING_START: (state: Loader) => {
    state.isLoading = true;
  },
  LOADING_FINISHED: (state: Loader) => {
    state.isLoading = false;
  },
};
const actions = {
  loadingStart: ({ commit }: {commit: Function}) => {
    commit('LOADING_START');
  },
  loadingFinished: ({ commit }: {commit: Function}) => {
    commit('LOADING_FINISHED');
  },
};
const getters = {
  isLoading: (state: Loader) => state.isLoading,
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
